var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 mb-5"},[_vm._m(0),_vm._m(1),_c('p',{staticClass:"mb-5"},[_vm._v("La caracterización de usuarios en los servicios de salud es fundamental para ofrecer una atención adecuada y humanizada. Conocer sus condiciones socioeconómicas y normativas permite adaptar los servicios a sus necesidades, mejorando la calidad y eficiencia del sistema. Esta práctica contribuye a garantizar derechos, promover la equidad y fortalecer la confianza en el sistema de salud, beneficiando a toda la sociedad. La correcta atención a los usuarios es clave para construir un sistema más justo, inclusivo y efectivo, que responda a las demandas y expectativas de la población.")]),_vm._m(2),_c('p',{staticClass:"mb-5"},[_vm._v("El Sistema General de Seguridad Social en Salud (SGSSS) de Colombia, es un marco integral que combina aspectos normativos, institucionales, financieros y de gestión para garantizar que toda la población tenga acceso a servicios de salud de calidad, oportunos y adecuados. Este sistema se fundamenta en el reconocimiento del derecho a la salud, establecido en la Constitución Política de Colombia, el cual busca promover la protección social en salud, mediante la integración de diferentes actores, recursos y acciones coordinadas para mejorar la calidad de vida de los ciudadanos y reducir las desigualdades en el acceso a los servicios de salud.")]),_vm._m(3),_c('p',{staticClass:"mb-5"},[_vm._v("A continuación, se relacionan los aspectos que se deben tener en cuenta en la estructuración del Sistema General de Seguridad Social en Salud: ")]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-8 order-lg-1 order-2"},[_c('AcordionA',{attrs:{"tipo":"b","clase-tarjeta":"tarjeta tarjeta--gris"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Afiliación"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Es el proceso mediante el cual las personas se inscriben formalmente en el sistema, ya sea en el régimen contributivo, para quienes tienen capacidad de pago, o en el régimen subsidiado, dirigido a las personas en situación de pobreza o vulnerabilidad. La afiliación garantiza que los individuos tengan derecho a recibir servicios de salud y a participar en la financiación del sistema, promoviendo la inclusión social y la protección de los derechos de todos los ciudadanos, independientemente de su condición socioeconómica.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Financiación"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Es el mecanismo mediante el cual se recaudan y distribuyen los recursos necesarios para sostener el sistema. Incluye las cotizaciones de los afiliados en el régimen contributivo, los aportes del Estado para el régimen subsidiado, y otros recursos públicos y privados destinados a garantizar la cobertura y la calidad de los servicios. La sostenibilidad financiera del sistema es fundamental para mantener la continuidad y la expansión de la cobertura, así como para garantizar la equidad en la distribución de los recursos.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Prestación de servicios"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Se refiere a la oferta de atención médica, preventiva, de recuperación y rehabilitación, brindada por instituciones autorizadas y profesionales capacitados. Estas instituciones deben cumplir con estándares de calidad, contar con infraestructura adecuada y estar sujetas a regulaciones que aseguren la eficiencia y la protección de los derechos de los usuarios. La prestación de servicios abarca desde la atención primaria en salud hasta procedimientos especializados y hospitalarios, buscando una atención integral y centrada en el paciente.")])])]),_c('div',{staticClass:"row",attr